* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f0f0f0;
}

.container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    max-width: 1040px;
    margin: 50px auto;
}

.image-container {
    position: relative;
    max-width: 300px;

    &:hover{
        .overlay {
            opacity: 1;
        }
    }
}

.image {
    width: 100%;
    border-radius: 8px;
    transition: opacity 0.3s ease, filter 0.3s ease;
    cursor: pointer;
}

.grayscale {
    filter: grayscale(100%);
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    font-size: 20px;
    border-radius: 8px;
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
}

.clicked {
    filter: grayscale(0%);
    opacity: 1; 
}
